import React  from 'react'
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from './Components/Home/Home';
import AddCandidate from './Components/Admin/AddCandidate/AddCandidate';
import Voting from './Components/Voting/Voting';
import Result from './Components/Result/Result';
import AddAdmin from './Components/Admin/AddAdmin/AddAdmin'
import Registration from './Components/Registration/Registration'
import Verification from './Components/Admin/Verification/Verification'

import NotFound from './Components/NotFound';

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path={process.env.PUBLIC_URL + "/"} component={Home} />
          <Route exact path={process.env.PUBLIC_URL +"/AddCandidate"} component={AddCandidate} />
          <Route exact path={process.env.PUBLIC_URL +"/Voting"} component={Voting} />
          <Route exact path={process.env.PUBLIC_URL +"/Result"} component={Result} />
          <Route exact path={process.env.PUBLIC_URL +"/AddAdmin"} component={AddAdmin} />
          <Route exact path={process.env.PUBLIC_URL +"/Registration"} component={Registration} />
          <Route exact path={process.env.PUBLIC_URL +"/Verification"} component={Verification} />
          <Route exact path={process.env.PUBLIC_URL +"*"} component={NotFound} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
